const reducer = (state, action) => {
    switch (action.type) {
        case "SET_SETTINGS":
            return {
                ...state,
                settings: state.settings.map((set) => {
                    if (set.field_name === action.payLoad.field_name) {
                        return action.payLoad;
                    } else {
                        return set;
                    }
                }),
            };
        case "SET_DATA":
            return {
                ...state,
                data: action.payLoad,
            };
        default:
            return state;
    }
};

export default reducer;
